<template>
  <div   :style="{'background-image': `url(${require('@/assets/header/head_background.png')})`}">

  <div class="registration-container" >
    <div class="registration-content">
      <h1>Welcome</h1>
      <p>Join Us!</p>
      <div class="logo">
        <img src="@/assets/header/head_background.png" alt="Logo">
      </div>
      <h2 class="form-title"></h2>
      <form @submit.prevent="register">
        <div class="form-group">
          <input type="text" placeholder="" v-model="registerForm.username">
        </div>
        <div class="form-group">
          <input type="password" placeholder="" v-model="registerForm.password">
        </div>
        <div class="form-group">
          <input type="password" placeholder="" v-model="registerForm.confirmPassword">
        </div>
        <div class="form-group">
          <input type="email" placeholder="" v-model="registerForm.email">
        </div>
        <button type="submit"></button>
      </form>
      <p class="switch-link" @click="switchToLogin"></p>
    </div>
  </div>
  </div>

</template>

<script>
import axios from "axios";
import api from "@/api/api";

export default {
  data() {
    var checkAge = (rule, value, callback) => {
      if (!value) {
        return callback(new Error(''));
      }
      setTimeout(() => {
        if (!Number.isInteger(value)) {
          callback(new Error(''));
        } else {
          if (value < 15) {
            callback(new Error('15'));
          } else {
            callback();
          }
        }
      }, 1000);
    };
    var checkEmail = (rule, value ,callback) => {
      if (!value){
        return callback(new Error(""));
      }else{
        var emailPattern =  /^[\w-]+(.[\w-]+)*@[\w-]+(.[\w-]+)+$/; // 
        if(!emailPattern.test(value)){
          return callback(new Error(""))
        }else{
          callback();
        }

      }
    }
    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''));
      } else {
        if (this.ruleForm.checkPass !== '') {
          this.$refs.ruleForm.validateField('checkPass');
        }
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''));
      } else if (value !== this.ruleForm.password) {
        callback(new Error('!'));
      } else {
        callback();
      }
    };


    return {
      ruleForm: {
        name:'',
        email:'232313232@qq.com',
        age: 21,
        password: '',
        checkPass: ''
      },
      rules: {
        email: [
          { validator: checkEmail, trigger: "blur" }
        ],
        password: [
          { validator: validatePass, trigger: "blur" }
        ],
        checkPass: [
          { required: true, message: "", trigger: "blur" },
          { validator: validatePass2, trigger: "blur" }
        ],
        age: [
          { validator: checkAge, trigger: "blur" }
        ]
      }

    };
  },
  methods: {
    reg_submitForm(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          try {
            const response = await api.register(this.ruleForm)

            if (response.data.code === '0'){
              alert('!');
              await this.$router.push('/login')
            }else if(response.data.code === '10000') {
              alert('');
            }

          }catch (error){
            console.log(error)
          }


        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
}
</script>

