<template>
  <div>
    <h2></h2>
    <el-form :inline="true" :model="queryForm" class="query-form">
      <el-form-item label="">
        <el-input v-model="queryForm.name"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="queryForm.category" placeholder="">
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="queryForm.minPrice" placeholder="" style="width: 100px;"></el-input>~
        <el-input v-model="queryForm.maxPrice" placeholder="" style="width: 100px;"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="doQuery"></el-button>
      </el-form-item>
    </el-form>

    <el-table :data="fruitList" style="width: 100%">
      <el-table-column prop="id" label="" width="80"></el-table-column>
      <el-table-column prop="name" label=""></el-table-column>
      <el-table-column prop="category" label=""></el-table-column>
      <el-table-column prop="description" label=""></el-table-column>
      <el-table-column prop="price" label="" width="120"></el-table-column>
    </el-table>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'FruitQuery',
  data() {
    return {
      queryForm: {
        name: '',
        category: '',
        minPrice: '',
        maxPrice: ''
      },
      fruitList: []
    }
  },
  methods: {
    doQuery() {
      axios.post('http://localhost:8088/api/product/search',
          this.queryForm,

          {
            headers: {
              'Content-Type': 'application/json'
            }
          }
      ).then(res => {
        this.fruitList = res.data
      })
    }
  }
}
</script>

<style scoped>
.query-form {
  margin-bottom: 20px;
}
</style>
